
.aside-box{
	background-color: #545c64;
}

.el-menu{
	border-right: none;
}
#aside-box{
	height: 100%;
	overflow: hidden;
	position: relative;
}
#scroll{ width:5px; position:absolute; right:0; top:0; opacity:0; -webkit-transition:opacity .3s; transition:opacity .3s;
}
#scrollBar{ width:5px; background:#76839E; position:absolute; left:0; border-radius:5px;
}
#aside-box:hover #scroll{ opacity:1;
}












































































































.img-file{
	display: none;
